<script>
import { tns } from '@/../node_modules/tiny-slider/src/tiny-slider';
import { chkPattern } from '@/common/vue-mixins';
import InnerEmulatorSns from './InnerEmulatorSns';

export default {
	name: 'emulatorViewSns',
	props: {
		isGuideType: {
			type: String,
			default: '',
		},
		pageType: {
			type: String,
			default: '',
		}
	},
	mixins: [chkPattern],
	data() {
		return {
			layerId: '',
			cardCount: 2,
			isShowCarousel: true,
			isShowCard: true,
			isShowHighlighted: false,
		};
	},
	components: {
		InnerEmulatorSns,
	},
	mounted() {
		this.initCarousel();
	},
	methods: {
		changeLayerId(layerId) {
			this.layerId = layerId;
		},
		initCarousel() {
			// carousel
			if (this.pageType == 'carousel') {
				var carousel = tns({
					container: '.carousel',
					items: 1,
					fixedWidth: 248,
					slideBy: 1,
					autoplay: false,
					controls: false,
					nav: false,
					mouseDrag: false,
					loop: false,
					gutter: 10
				});
			}

		}
	},
	computed: {

	}
};
